<template>
  <div class="d-flex">
    <NavbarBack />
    <div class="container m-5 ">
    <ToastMessages></ToastMessages>
    <router-view />
   </div>
  </div>
</template>
<script>
import emitter from "@/methods/emitter";
import $httpMessageState from "@/methods/pushMessageState";
import ToastMessages from "@/components/back/ToastMessages.vue";
import NavbarBack from "@/components/back/NavbarBack.vue";
// 1.將 Token 加入 Headers 驗證
export default {
  components: {
    NavbarBack,
    ToastMessages,
  },
  provide() {
    return {
      emitter,
      $httpMessageState,
    }
  },
  created() {
    const token = document.cookie.replace(
      /(?:(?:^|.*;\s*)token\s*=\s*([^;]*).*$)|^.*$/,
      "$1"
    );
    // console.log(token);
    this.$http.defaults.headers.common["Authorization"] = token;

    // 2.檢查用戶是否仍持續登入
    const api = `${process.env.VUE_APP_API}api/user/check`;
    this.$http
      .post(api, this.user)
      .then((res) => {
        // console.log(res);
        if (!res.data.success) {
          this.$router.push("/login");
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },
};
</script>
